
#titleBar{
  display: flex;
  text-align: center;
  padding-top: 1.5rem;
}

.container{
  background-color: #EDEDED;
}

.pageFunctionTitle {
  margin-top: 1rem;
}

#appTitle{
  margin:auto;
  position: relative;
}

#adminNavTab {
  margin-left: auto;
}

#csgNavLogo{
  height: 150px;
  width: 150px;
}

.App {
  text-align: center;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

footer {

  position:relative;
  bottom:0;
  width: 100%;
}


#footInfo{
  justify-self: center;
}

#headerNav{
  font-size: 1.25rem;
}

#footerNav {
  font-size: 1rem;
}

#builtWith {

  align-items: center;
}

#builtWithP {
  margin: 0;
}

#reactPNG {
  height: 50px;
  width: 50px;
}

#mainContent {
  min-height: 70vw;
  min-height: -webkit-calc(100% - 186PX);
  min-height: -moz-calc(100% -186px);
  min-height: calc(100% - 186px);
}

.placeHolder {
  max-width: 100%;
  object-fit: fill;
}

